<template>
<div class='topListCon'>
  <div class="topLis">
    <ol class="switch-tab inner">
      <li class="current">
        <a href="https://www.wandoujia.com/top/game">排行榜</a>
      </li>
    </ol>
  </div>
  <div class="gameListCon inner">
    <game-list :GameList ='gameList' />
    <button class="watchMore" @click="loadData()">查看更多</button>
  </div>
</div>
</template>
<script>
import gameList from '@/components/gameList'
export default {
  components: {
    gameList
  },
  data: function () {
    return {
      name: '123',
      gameList: [1, 2, 3]
    }
  },
  methods: {
    loadData () {
      console.log(1)
      for (var i = 0; i < 4; i++) {
        this.gameList.push(i)
      }
    }
  }
}
</script>
<style lang="scss" scoped>
.topListCon {
  background: #f5f5f5;
  padding-top: 20px;
  .switch-tab {
    text-align: center;
    height: 32px;
    line-height: 32px;
    border-bottom: 2px solid #4cc8b5;
    overflow: hidden;
    margin: 0 auto;
    li {
      text-align: center;
      display: inline-block;
      a{
        font-size: 14px;
        display: inline-block;
        color: #666;
        padding: 0 17px;
      }
      &.current a {
        color: #fff;
        background: #4cc8b5;
        font-size: 0.875em;
      }
    }
  }
}
.gameListCon {
  padding: 0 20px 70px 0;
  position: relative;
}
.watchMore {
  margin: 23px 0;
  position: absolute;
  left: 50%;
  margin-left: -48px;
  color: #333333;
  background: #ddd;
  border: none;
  line-height: 35px;
  font-size: 14px;
  width: 96px;
}
</style>



